'use strict';

var VJS = VJS || {};
VJS.helpers = VJS.helpers || {};

/*** Imports ***/

VJS.geometries = VJS.geometries || {};
VJS.geometries.slice = VJS.geometries.slice || require('../geometries/geometries.slice');

VJS.shaders = VJS.shaders || {};
VJS.shaders.data = VJS.shaders.data || require('../shaders/shaders.data');





//
// https://en.wikipedia.org/wiki/Immediately-invoked_function_expression
VJS.helpers.slice = function() {

  THREE.Object3D.call(this);

  // ...
  this._series = null;
  this._uniforms = null;
  this._frameIndex = null;
  this._slice = null;
  this._border = null;

  //
  this._autoWindowLevel = false;

};

VJS.helpers.slice.prototype = Object.create(THREE.Object3D.prototype);

VJS.helpers.slice.prototype.constructor = VJS.helpers.slice;

VJS.helpers.slice.prototype.merge = function(seriesHelper) {
  return this._series.merge(seriesHelper._series);
};

VJS.helpers.slice.prototype.addSeries = function(series) {
  this._series = series;
};

VJS.helpers.slice.prototype.getStack = function(stackIndex) {
  return stackIndex;
};

VJS.helpers.slice.prototype.prepare = function() {

  window.console.log('helpers Series Prepare!!!');
  if (this._series) {

    // get first stack!
    var stack = this._series._stack[0];
    stack.prepare();
    window.console.log(stack);

    // Convenience function
    var dimensions = stack._dimensions;
    var halfDimensions = stack._halfDimensions;

    // voxel offset
    var offset = new THREE.Vector3(-0.5, -0.5, -0.5);

    // Bounding Box
    var geometry = new THREE.BoxGeometry(
        dimensions.x, dimensions.y, dimensions.z);
    geometry.applyMatrix(new THREE.Matrix4().makeTranslation(
        halfDimensions.x + offset.x, halfDimensions.y + offset.y, halfDimensions.z + offset.z));
    geometry.applyMatrix(stack._ijk2LPS);
    var material = new THREE.MeshBasicMaterial({
      wireframe: true,
      color: 0x61F2F3
    });
    var cube = new THREE.Mesh(geometry, material);
    this.add(cube);

    // Slice
    // Geometry
    //

    // Define the bouding box used to generate the slice geometry
    // center
    // orientation
    // and half-dimensions
    var center = new THREE.Vector3(0, 0, 0);
    var orientation = new THREE.Vector3(
        new THREE.Vector3(1, 0, 0),
        new THREE.Vector3(0, 1, 0),
        new THREE.Vector3(0, 0, 1));

    var position = new THREE.Vector3(
      Math.floor(stack._halfDimensions.x),
      Math.floor(stack._halfDimensions.y),
      Math.floor(stack._halfDimensions.z) + 0.5 - stack._halfDimensions.z
    );

    var direction = new THREE.Vector3(0, 0, 1);

    var sliceGeometry = new VJS.geometries.slice(
        halfDimensions, center, orientation,
        position, direction);
    sliceGeometry.applyMatrix(new THREE.Matrix4().makeTranslation(
        halfDimensions.x + offset.x, halfDimensions.y + offset.y, halfDimensions.z + offset.z));
    sliceGeometry.applyMatrix(stack._ijk2LPS);

    // update _framIndex
    this._frameIndex = Math.floor(halfDimensions.z);

    // Slice
    // Material
    var textures = [];
    for (var m = 0; m < stack._nbTextures; m++) {
      var tex = new THREE.DataTexture(stack._rawData[m], stack._textureSize, stack._textureSize, THREE.RGBAFormat, THREE.UnsignedByteType, THREE.UVMapping, THREE.ClampToEdgeWrapping, THREE.ClampToEdgeWrapping, THREE.NearestFilter, THREE.NearestFilter);
      tex.needsUpdate = true;
      textures.push(tex);
    }

    var sliceMaterial = new THREE.ShaderMaterial({
      // 'wireframe': true,
      'side': THREE.DoubleSide,
      'transparency': true,
      'uniforms': VJS.shaders.data.parameters.uniforms,
      'vertexShader': "#define GLSLIFY 1\nvarying vec4 vPos;\n\n//\n// main\n//\nvoid main() {\n\n  vPos = modelMatrix * vec4(position, 1.0 );\n  gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0 );\n\n}",
      'fragmentShader': "#define GLSLIFY 1\nuniform float uTextureSize;\nuniform float uWindowLevel[2];\nuniform sampler2D uTextureContainer[7];\nuniform vec3 uDataDimensions;\nuniform mat4 uWorldToData;\nuniform int uNumberOfChannels;\nuniform int uBitsAllocated;\nuniform int uInvert;\n\n// hack because can not pass arrays if too big\n// best would be to pass texture but have to deal with 16bits\nuniform int uLut;\nuniform float uLutI[16]; // 16 * 4 (intesity - r- g - b)\nuniform float uLutR[16];\nuniform float uLutG[16];\nuniform float uLutB[16];\n\nvarying vec4 vPos;\n\nvec4 sampleAs3DTexture(in vec3 textureCoordinate) {\n\n  float slicePixelSize = 1.0 / uTextureSize;\n  // Model coordinate (IJK) to data index\n  float index = textureCoordinate.x * uDataDimensions.x + textureCoordinate.y * uDataDimensions.y * uDataDimensions.x + textureCoordinate.z * uDataDimensions.z * uDataDimensions.y * uDataDimensions.x;\n\n  // Map data index to right sampler2D texture\n  float textureIndex = floor(index / (uTextureSize*uTextureSize));\n  float inTextureIndex = mod(index, uTextureSize*uTextureSize);\n\n  // Get row and column in the texture\n  float rowIndex = floor(inTextureIndex/uTextureSize);\n  float colIndex = mod(inTextureIndex, uTextureSize);\n\n  // Map row and column to uv\n  vec2 uv = vec2(0,0);\n  uv.x = slicePixelSize * 0.5 + colIndex * slicePixelSize;\n  uv.y = 1.0 - (slicePixelSize * 0.5 + rowIndex * slicePixelSize);\n\n  vec4 dataValue = vec4(0, 0, 0, 1);\n  if(textureIndex == 0.0){\n    dataValue = texture2D(uTextureContainer[0], uv);\n  }\n  else if(textureIndex == 1.0){\n    dataValue = texture2D(uTextureContainer[1], uv);\n  }\n  else if(textureIndex == 2.0){\n    dataValue = texture2D(uTextureContainer[2], uv);\n  }\n  else if(textureIndex == 3.0){\n    dataValue = texture2D(uTextureContainer[3], uv);\n  }\n  else if(textureIndex == 4.0){\n    dataValue = texture2D(uTextureContainer[4], uv);\n  }\n  else if(textureIndex == 5.0){\n    dataValue = texture2D(uTextureContainer[5], uv);\n  }\n  else if(textureIndex == 6.0){\n    dataValue = texture2D(uTextureContainer[6], uv);\n  }\n  // else if(textureIndex == 7.0){\n  //   dataValue = texture2D(uTextureContainer[7], uv);\n  // }\n  // else if(textureIndex == 8.0){\n  //   dataValue = texture2D(uTextureContainer[8], uv);\n  // }\n  // else if(textureIndex == 9.0){\n  //   dataValue = texture2D(uTextureContainer[9], uv);\n  // }\n  // else if(textureIndex == 10.0){\n  //   dataValue = texture2D(uTextureContainer[10], uv);\n  // }\n  // else if(textureIndex == 11.0){\n  //   dataValue = texture2D(uTextureContainer[11], uv);\n  // }\n  // else if(textureIndex == 12.0){\n  //   dataValue = texture2D(uTextureContainer[12], uv);\n  // }\n  // else if(textureIndex == 13.0){\n  //   dataValue = texture2D(uTextureContainer[13], uv);\n  // }\n  // else if(textureIndex == 14.0){\n  //   dataValue = texture2D(uTextureContainer[14], uv);\n  // }\n  // else if(textureIndex == 15.0){\n  //   dataValue = texture2D(uTextureContainer[15], uv);\n  // }\n  // else {\n  //   discard;\n  // }\n\n  return dataValue;\n}\n\nvoid main(void) {\n\n  // get texture coordinates of current pixel\n  // might not be the right way to do it:\n  // precision issues ar voxels limits\n  // need to add machine epsilon?\n  vec4 dataCoordinateRaw = uWorldToData * vPos;\n  dataCoordinateRaw += 0.5;\n  vec3 dataCoordinate = vec3(floor(dataCoordinateRaw.x), floor(dataCoordinateRaw.y), floor(dataCoordinateRaw.z));\n\n\n  // if data in range, look it up in the texture!\n  if(dataCoordinate.x >= 0.0\n  && dataCoordinate.y >= 0.0\n  && dataCoordinate.z >= 0.0\n  && dataCoordinate.x < uDataDimensions.x\n  && dataCoordinate.y < uDataDimensions.y\n  && dataCoordinate.z < uDataDimensions.z\n  ){\n    vec3 textureCoordinate = dataCoordinate/uDataDimensions;\n    vec4 dataValue = sampleAs3DTexture(textureCoordinate);\n    //vec4(1.0, 0.5, 0.5, 1.0);//\n    \n\n    // Threshold? to copr intensities out?\n\n    if(uNumberOfChannels == 1){\n      // reconstruct 16bits data if any\n      float rawValue = dataValue.r * 255.0 * 256.0 + dataValue.g * 255.0;\n      float windowMin = uWindowLevel[0] - uWindowLevel[1]/2.0;\n      float windowMax = uWindowLevel[0] + uWindowLevel[1]/2.0;\n      float combined = ( rawValue - windowMin ) / uWindowLevel[1];\n\n      dataValue.r = dataValue.g = dataValue.b = combined;\n    }\n\n    // Apply LUT table...\n    // will pass it through a texture...\n\n    if(uLut == 1){\n    // if uApplyLUT\n    // if LUT Gradation? Interpolation\n    // get LUT from texture\n    for(int i=0; i<16; i++){\n      if(dataValue.r < uLutI[i]){\n        // i and i-1\n        float dataValuePosition = dataValue.r - uLutI[i-1];\n        float lutRange = uLutI[i] - uLutI[i-1];\n        float lutWeight = dataValuePosition/lutRange;\n        // use gradation or not??\n        // color distance\n        float colorDistanceR = uLutR[i] - uLutR[i-1];\n        float colorDistanceG = uLutG[i] - uLutG[i-1];\n        float colorDistanceB = uLutB[i] - uLutB[i-1];\n\n        // by weight\n        float colorIncrementR = lutWeight * colorDistanceR;\n        float colorIncrementG = lutWeight * colorDistanceG;\n        float colorIncrementB = lutWeight * colorDistanceB;\n\n        // add it\n        dataValue.r = uLutR[i-1] + colorIncrementR;\n        dataValue.g = uLutG[i-1] + colorIncrementG;\n        dataValue.b = uLutB[i-1] + colorIncrementB;\n        // dataValue.r += colorIncrementR;\n        // dataValue.g += colorIncrementG;\n        // dataValue.b += colorIncrementB;\n       break;\n     }\n    }\n    }\n\n    // Apply label map...?\n    // target specific intensity\n\n    if(uInvert == 1){\n      dataValue = vec4(1, 1, 1, 1) - dataValue;\n      dataValue.a = 1.0;\n    }\n\n    gl_FragColor = dataValue;\n  }\n  else{\n    // should be able to choose what we want to do if not in range:\n    // discard or specific color\n    //discard;\n    gl_FragColor = vec4(0.011, 0.662, 0.956, 1.0);\n  }\n}"
    });

    // important so uniforms are not overwritten!
    // clone it!
    var mySliceMaterial = sliceMaterial.clone();
    this._uniforms = mySliceMaterial.uniforms;
    this._uniforms.uTextureSize.value = stack._textureSize;
    this._uniforms.uTextureContainer.value = textures;
    // texture dimensions
    this._uniforms.uDataDimensions.value = stack._dimensions;
    // world to model
    this._uniforms.uWorldToData.value = stack._lps2IJK;
    // window level
    this._uniforms.uWindowLevel.value = stack._windowLevel;
    this._uniforms.uNumberOfChannels.value = stack._numberOfChannels;
    this._uniforms.uBitsAllocated.value = stack._bitsAllocated;
    this._uniforms.uInvert.value = stack._invert;

    this._slice = new THREE.Mesh(sliceGeometry, mySliceMaterial);
    this.add(this._slice);

    // Border of the slice
    var borderMaterial = new THREE.LineBasicMaterial({
      color: 0xff0000,
      polygonOffset: true,
      polygonOffsetFactor: -0.1
    });
    var borderGeometry = new THREE.Geometry();
    for (var i = 0; i < sliceGeometry.vertices.length; i++) {
      borderGeometry.vertices.push(sliceGeometry.vertices[i]);
    }
    borderGeometry.vertices.push(sliceGeometry.vertices[0]);

    // borderGeometry.vertices = sliceGeometry.vertices;
    this._border = new THREE.Line(borderGeometry, borderMaterial);
    this.add(this._border);

  } else {
    window.console.log('no series to be prepared...');
  }
};

VJS.helpers.slice.prototype.updateSliceGeometry = function() {
  var stack = this._series._stack[0];
  var halfDimensions = stack._halfDimensions;
  // voxel offset
  var offset = new THREE.Vector3(-0.5, -0.5, -0.5);

  var center = new THREE.Vector3(0, 0, 0);
  var orientation = new THREE.Vector3(
      new THREE.Vector3(1, 0, 0),
      new THREE.Vector3(0, 1, 0),
      new THREE.Vector3(0, 0, 1));

  var position = new THREE.Vector3(
    0,
    0,
    this._frameIndex + 0.5 - stack._halfDimensions.z
    );

  var direction = new THREE.Vector3(0, 0, 1);

  var sliceGeometry = new VJS.geometries.slice(
      halfDimensions, center, orientation,
      position, direction);
  sliceGeometry.applyMatrix(new THREE.Matrix4().makeTranslation(
      halfDimensions.x + offset.x, halfDimensions.y + offset.y, halfDimensions.z + offset.z));
  sliceGeometry.applyMatrix(stack._ijk2LPS);

  // helper - update Geometry
  //is memory leaking???

  this._slice.geometry = sliceGeometry;
  this._slice.geometry.verticesNeedUpdate = true;
};

VJS.helpers.slice.prototype.updateBorderGeometry = function() {

  var borderGeometry = new THREE.Geometry();
  for (var i = 0; i < this._slice.geometry.vertices.length; i++) {
    borderGeometry.vertices.push(this._slice.geometry.vertices[i]);
  }
  borderGeometry.vertices.push(this._slice.geometry.vertices[0]);

  this._border.geometry.vertices = borderGeometry.vertices;
  this._border.geometry.verticesNeedUpdate = true;
};

// a delete method too!


/*** Exports ***/

var moduleType = typeof module;
if ((moduleType !== 'undefined') && module.exports) {
    module.exports = VJS.helpers.slice;
}